import { useNavigate } from "react-router-dom";
import "./style.scss";

const TabButton = (props) => {
    const navigate = useNavigate();
    return(
    <button onClick={() => {
        navigate(`/${props.name}`);
    }} className={`tab tab__${props.name}-btn`}>
        <div className="tab__name">{props.name === `fren` ? `Frens` : `Earn!`}</div>
    </button>
    )
}

export default TabButton;