import React, { useState, useEffect } from "react";
import Lottie from "lottie-react";
import ContentLoader from "react-content-loader";
import Influencer from "../../assets/animation/Influencer.json";
import { BrowserRouter as Router, Route, useNavigate } from "react-router-dom";
import { CSSTransition } from "react-transition-group";
import "./style.scss";
import UsersSkeleton from "../../components/UsersSkeleton";
import { BackButton } from "@twa-dev/sdk/react";
import WebApp from "@twa-dev/sdk";

export default function PartyKings() {
  const navigate = useNavigate();

  const [playAnimation, setPlayAnimation] = useState(false);
  const [animationKey, setAnimationKey] = useState(0);

  const [userLoaded, setUserLoaded] = useState(false);
  useEffect(() => {
    setTimeout(() => {
      // setUserLoaded(true);
    }, 5000);
  }, []);

  const handleButtonClick = () => {
    setPlayAnimation(true);
    setAnimationKey((prevKey) => prevKey + 1);
  };

  const [showPage, setShowPage] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowPage(true);
    }, 50); // Задержка перед появлением страницы
  }, []);

  const [isVisible, setIsVisible] = useState(true);

  return (
    <>
      <BackButton onClick={() => navigate("/")} />
      <CSSTransition
        in={showPage}
        timeout={50}
        className="referral-animation"
        unmountOnExit
      >
        <div>
          <div className="page scrolling">
            <div className="background-influencer"></div>
            <div className="container position-relative">
              <div className="page__content">
                <div className="sGES">
                  <div onClick={handleButtonClick}>
                    <CSSTransition
                      in={isVisible}
                      timeout={100}
                      className={`slide-down-enter`}
                      unmountOnExit
                    >
                      <div>
                        <Lottie
                          key={animationKey}
                          loop={false}
                          autoPlay={playAnimation}
                          animationData={Influencer}
                          isStopped={!playAnimation}
                          className="Lbr3"
                        />
                      </div>
                    </CSSTransition>
                  </div>
                </div>
                <div className="m72u">Party Kings</div>
                <div className="uDr2">Invite more frens to get here</div>
                <div
                  onClick={() => {
                    WebApp.openTelegramLink(
                      "https://t.me/share/url?text=%F0%9F%8E%81%20%2B2.5k%20Pixelcoin%20as%20a%20first-time%20gift%0A%F0%9F%8E%81%F0%9F%8E%81%F0%9F%8E%81%20%2B50k%20Pixelcoin%20if%20you%20have%20Telegram%20Premium%20%40PremiumBot&url=https://t.me/pixelcoinbot/click?startapp=5668476910"
                    );
                  }}
                  className="ccBa"
                >
                  <div className="Jh2w">
                    <div className="Li2p">
                      <div className="Iy4o">Invite frens</div>
                    </div>
                  </div>
                </div>
                <div>
                  {!userLoaded ? (
                    <UsersSkeleton />
                  ) : (
                    <div>Here is the uploaded content</div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </CSSTransition>
    </>
  );
}
