import "./style.scss";
import Lottie from "lottie-react";
import Fire from "../../assets/animation/Fire.json";
import { getRandomKey } from "../../utils/getRandomKey";
import { shuffle } from "../../utils/shuffle";
import { useState } from "react";
import FieldItem from "../FieldItem";

const defaultItems = [
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 1,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 2,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
  {
    id: getRandomKey(),
    type: 3,
    isOpened: false,
  },
];

export default function Field() {
  const [items, setItems] = useState(defaultItems);

  const open = (item) => {
    if (item.type === 1) {
      items.forEach((item) => (item.isOpened = true));
    } else if (item.type === 2) {
      items.forEach((item) => (item.isOpened = true));
    } else {
      item.isOpened = true;
    }

    setItems([...items]);
  };
  return (
    // <Lottie animationData={Fire} />
    <div className="field-container">
      <ui className="field">
        {/* {shuffle(items).map((item) => { */}
        {items.map((item) => {
          return (
            <FieldItem
              item={item}
              onClick={() => {
                open(item);
              }}
            />
          );
        })}
      </ui>
    </div>
  );
}
