import React from "react";
import Lottie from "lottie-react";
import Cup from "../../assets/animation/Cup.json";
import {
  BrowserRouter as Router,
  Route,
  useNavigate,
  useLocation,
} from "react-router-dom";
import { useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import { Swiper, SwiperSlide } from "swiper/react";
import "./style.scss";
import { BackButton } from "@twa-dev/sdk/react";
import WebApp from "@twa-dev/sdk";
import UsersSkeleton from "../../components/UsersSkeleton";
import ProgressBar from "../../components/ProgressBar";
import addCommas from "../../utils/addCommas";
import PlayersList from "../../components/PlayersList";
import getLeague from "../../utils/getLeague";

function Search() {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const assetLeague = params.get("assetLeague");
  const type = params.get("type");
  return { assetLeague, type };
}

export default function League() {
  const navigate = useNavigate();

  const result = Search();

  const [playAnimation, setPlayAnimation] = useState(false);
  const [animationKey, setAnimationKey] = useState(0);

  const [userLoaded, setUserLoaded] = useState(false);
  useEffect(() => {
    setTimeout(() => {
      // setUserLoaded(true);
    }, 5000);
  }, []);

  const handleButtonClick = () => {
    setPlayAnimation(true);
    setAnimationKey((prevKey) => prevKey + 1);
  };

  const allPlayers = 0;
  const [balance, setBalance] = useState(() => {
    // При первом запуске компонента проверяем localStorage на наличие баланса
    const storedBalance = localStorage.getItem("balance");
    return storedBalance ? parseFloat(storedBalance) : 0;
  });

  useEffect(() => {
    // При изменении баланса сохраняем его в localStorage
    localStorage.setItem("balance", balance.toString());
  }, [balance]);

  const league = getLeague(balance);

  const [showPage, setShowPage] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowPage(true);
    }, 500); // Задержка перед появлением страницы
  }, []);

  const [showCup, setShowCup] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowCup(true);
    }, 0); // Задержка перед появлением страницы
  }, []);

  return (
    <>
      <BackButton onClick={() => navigate("/")} />
      <div className="page scrolling">
        <div
          className={`background-league theme-league__${result.assetLeague}`}
        ></div>
        <div className="container position-relative">
          <div className="page__content uY4S">
            <div onClick={() => navigate("/stats")} className="w4i3">
              <div>
                <div className="jYed IYe4"></div>
                <div className="Nzze"></div>
                <div className="jYed Kebc"></div>
                <div className="nEhw"></div>
                <div className="jYed IYUe"></div>
                <div className="Fp2m"></div>
                <div className="J4ec">{addCommas(allPlayers)} Pixelcoiners</div>
              </div>
              <div>
                <div className="JYcc">Stats</div>
                <div className="Yevc">
                  <svg
                    width="7"
                    height="12"
                    viewBox="0 0 7 12"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                    className="KYci"
                  >
                    <path
                      d="m1 1 5 5-5 5"
                      stroke="currentColor"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    ></path>
                  </svg>
                </div>
              </div>
            </div>
            <div>
              <div className="swiper">
                <div className="swiper-wrapper">
                  <div className="swiper-slide">
                    <div className="_QtZ">
                      <CSSTransition
                        in={showCup}
                        timeout={0}
                        className="slide-down-enter"
                        unmountOnExit
                      >
                        <div>
                          <div
                            className={`league-position league-icon__${result.assetLeague}`}
                          ></div>
                        </div>
                      </CSSTransition>
                      <button
                        onClick={() => {
                          result.assetLeague !== "bronze" &&
                            navigate(
                              `/league?assetLeague=${
                                (result.assetLeague === "silver" && "bronze") ||
                                (result.assetLeague === "gold" && "silver") ||
                                (result.assetLeague === "platinum" && "gold") ||
                                (result.assetLeague === "diamond" && "platinum")
                              }&type=${result.type}`
                            );
                        }}
                        className={`nav-btn ${
                          (result.assetLeague === "bronze" &&
                            "nav-btn_prev-disable") ||
                          "nav-btn_prev"
                        }`}
                      >
                        <svg
                          height="16px"
                          width="16px"
                          xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 492.004 492.004"
                          className={
                            (result.assetLeague === "bronze" && "I3cl") ||
                            "tVxl"
                          }
                        >
                          <g>
                            <g>
                              <path d="M382.678,226.804L163.73,7.86C158.666,2.792,151.906,0,144.698,0s-13.968,2.792-19.032,7.86l-16.124,16.12    c-10.492,10.504-10.492,27.576,0,38.064L293.398,245.9l-184.06,184.06c-5.064,5.068-7.86,11.824-7.86,19.028    c0,7.212,2.796,13.968,7.86,19.04l16.124,16.116c5.068,5.068,11.824,7.86,19.032,7.86s13.968-2.792,19.032-7.86L382.678,265    c5.076-5.084,7.864-11.872,7.848-19.088C390.542,238.668,387.754,231.884,382.678,226.804z" />
                            </g>
                          </g>
                        </svg>
                      </button>
                      <button
                        onClick={() => {
                          result.assetLeague !== "diamond" &&
                            navigate(
                              `/league?assetLeague=${
                                (result.assetLeague === "bronze" && "silver") ||
                                (result.assetLeague === "silver" && "gold") ||
                                (result.assetLeague === "gold" && "platinum") ||
                                (result.assetLeague === "platinum" && "diamond")
                              }&type=${result.type}`
                            );
                        }}
                        className={`nav-btn ${
                          (result.assetLeague === "diamond" &&
                            "nav-btn_next-disable") ||
                          "nav-btn_next"
                        }`}
                      >
                        <svg
                          height="16px"
                          width="16px"
                          xmlns="http://www.w3.org/2000/svg"
                          viewBox="0 0 492.004 492.004"
                          className={
                            (result.assetLeague === "diamond" && "I3cl") ||
                            "tVxl"
                          }
                        >
                          <g>
                            <g>
                              <path d="M382.678,226.804L163.73,7.86C158.666,2.792,151.906,0,144.698,0s-13.968,2.792-19.032,7.86l-16.124,16.12    c-10.492,10.504-10.492,27.576,0,38.064L293.398,245.9l-184.06,184.06c-5.064,5.068-7.86,11.824-7.86,19.028    c0,7.212,2.796,13.968,7.86,19.04l16.124,16.116c5.068,5.068,11.824,7.86,19.032,7.86s13.968-2.792,19.032-7.86L382.678,265    c5.076-5.084,7.864-11.872,7.848-19.088C390.542,238.668,387.754,231.884,382.678,226.804z" />
                            </g>
                          </g>
                        </svg>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div>
              <div className="P8Ey">
                <div className="og0c">
                  {(result.assetLeague === "bronze" && "Bronze league") ||
                    (result.assetLeague === "silver" && "Silver league") ||
                    (result.assetLeague === "gold" && "Gold league") ||
                    (result.assetLeague === "platinum" && "Platinum league") ||
                    (result.assetLeague === "diamond" && "Diamond league")}
                </div>
              </div>
            </div>
            <div className="Jh2w bgqq">
              <div className="Zo7S">
                {(result.type == "miners" &&
                  result.assetLeague === "bronze" &&
                  league === "bronze" &&
                  `${addCommas(balance)} / 30K`) ||
                  (result.type == "miners" &&
                    result.assetLeague === "silver" &&
                    league === "silver" &&
                    `${addCommas(balance - 30000)} / 20K`) ||
                  (result.type == "miners" &&
                    result.assetLeague === "gold" &&
                    league === "gold" &&
                    `${addCommas(balance - 50000)} / 50K`) ||
                  (result.type == "miners" &&
                    result.assetLeague === "platinum" &&
                    league === "platinum" &&
                    `${addCommas(balance - 100000)} / 200K`) ||
                  (result.type == "miners" &&
                    result.assetLeague === "diamond" &&
                    league === "diamond" &&
                    `${addCommas(balance - 300000)} / ∞`) ||
                  (result.type == "miners" &&
                    result.assetLeague === "bronze" &&
                    "from 0 Pixelcoin") ||
                  (result.type === "miners" &&
                    result.assetLeague === "silver" &&
                    "from 30K Pixelcoin") ||
                  (result.type === "miners" &&
                    result.assetLeague === "gold" &&
                    "from 50K Pixelcoin") ||
                  (result.type === "miners" &&
                    result.assetLeague === "platinum" &&
                    "from 100K Pixelcoin") ||
                  (result.type === "miners" &&
                    result.assetLeague === "diamond" &&
                    "from 300K Pixelcoin") ||
                  (result.type === "squads" &&
                    result.assetLeague === "bronze" &&
                    "from 0 Pixelcoin") ||
                  (result.type === "squads" &&
                    result.assetLeague === "silver" &&
                    "from 300K Pixelcoin") ||
                  (result.type === "squads" &&
                    result.assetLeague === "gold" &&
                    "from 500K Pixelcoin") ||
                  (result.type === "squads" &&
                    result.assetLeague === "platinum" &&
                    "from 1M Pixelcoin") ||
                  (result.type === "squads" &&
                    result.assetLeague === "diamond" &&
                    "from 3M Pixelcoin")}
              </div>
            </div>
            {result.assetLeague === "bronze" &&
            league === "bronze" &&
            result.type === "miners" ? (
              <div className="Ug3o">
                <ProgressBar balance={balance} />
              </div>
            ) : result.assetLeague === "silver" &&
              league === "silver" &&
              result.type === "miners" ? (
              <div className="Ug3o">
                <ProgressBar balance={balance} />
              </div>
            ) : result.assetLeague === "gold" &&
              league === "gold" &&
              result.type === "miners" ? (
              <div className="Ug3o">
                <ProgressBar balance={balance} />
              </div>
            ) : result.assetLeague === "platinum" &&
              league === "platinum" &&
              result.type === "miners" ? (
              <div className="Ug3o">
                <ProgressBar balance={balance} />
              </div>
            ) : result.assetLeague === "diamond" &&
              league === "diamond" &&
              result.type === "miners" ? (
              <div className="Ug3o">
                <ProgressBar balance={balance} />
              </div>
            ) : (
              <div className="Iued"></div>
            )}

            <div className="LibrarySegmentedControl LibrarySegmentedControl--state-fixed LibrarySegmentedControl--glider">
              <div
                onClick={() => {
                  navigate(
                    `/league?assetLeague=${result.assetLeague}&type=miners`
                  );
                }}
                className={`LibrarySegmentedControlItem__link ${
                  (result.type === "miners" &&
                    "LibrarySegmentedControlItem__link--active") ||
                  ""
                }`}
              >
                <div
                  className={`LibrarySegmentedControlItem ${
                    (result.type === "miners" &&
                      "LibrarySegmentedControlItem--active") ||
                    ""
                  }`}
                >
                  <div className="Typography LibrarySegmentedControlItem__inner LibraryLabel LibraryLabel--l-1 LibraryLabel--w-semi-bold">
                    Miners
                  </div>
                </div>
              </div>
              <div
                onClick={() => {
                  navigate(
                    `/league?assetLeague=${result.assetLeague}&type=squads`
                  );
                }}
                className={`LibrarySegmentedControlItem__link ${
                  (result.type === "squads" &&
                    "LibrarySegmentedControlItem__link--active") ||
                  ""
                }`}
              >
                <div
                  className={`LibrarySegmentedControlItem ${
                    (result.type === "squads" &&
                      "LibrarySegmentedControlItem--active") ||
                    ""
                  }`}
                >
                  <div className="Typography LibrarySegmentedControlItem__inner LibraryLabel LibraryLabel--l-1 LibraryLabel--w-semi-bold">
                    Squads
                  </div>
                </div>
              </div>
              <div
                className="LibrarySegmentedControl__glider"
                style={{
                  width: "calc(50% - 6px)",
                  transform:
                    (result.type === "miners" &&
                      "translateX(calc(0% + 0px))") ||
                    (result.type === "squads" &&
                      "translateX(calc(100% + 4px))"),
                }}
              ></div>
            </div>
            {(result.assetLeague !== "bronze" &&
              result.type === "miners" &&
              (!userLoaded ? (
                <UsersSkeleton />
              ) : (
                // <div className="UIx1">
                //   <div className="lgqO">
                //     <div className="GWMq">🫧</div>
                //     <div className="OCW2">There is nothing here</div>
                //   </div>
                // </div>
                <div>Content will be here</div>
              ))) ||
              (result.assetLeague !== "bronze" && result.type === "squads" && (
                <div className="UIx1">
                  <div className="lgqO">
                    <div className="GWMq">🫧</div>
                    <div className="OCW2">There is nothing here</div>
                  </div>
                </div>
              )) ||
              (result.assetLeague === "bronze" && (
                <div>
                  <div className="wDXF">
                    <div onClick={handleButtonClick} className="UQdA">
                      <Lottie
                        key={animationKey}
                        loop={false}
                        autoPlay={playAnimation}
                        animationData={Cup}
                        isStopped={!playAnimation}
                      />
                    </div>
                  </div>
                  <div className="NHDE">
                    {(result.type === "miners" && (
                      <div className="lGlN">
                        You'll join leaderboard
                        <br />
                        in the Silver League
                      </div>
                    )) ||
                      (result.type === "squads" && (
                        <div className="lGlN">
                          Leaderboard is under construction
                        </div>
                      ))}
                  </div>
                </div>
              ))}
          </div>
        </div>
      </div>
    </>
  );
}
