export default function Main() {
  const navigate = useNavigate();

  const [balance, setBalance] = useState(() => {
    // При первом запуске компонента проверяем localStorage на наличие баланса
    const storedBalance = localStorage.getItem("balance");
    return storedBalance ? parseFloat(storedBalance) : 0;
  });

  useEffect(() => {
    // При изменении баланса сохраняем его в localStorage
    localStorage.setItem("balance", balance.toString());
  }, [balance]);

  const handleIncrement = () => {
    setBalance((prevBalance) => prevBalance + 400);
  };

  const handleDecrement = () => {
    setBalance((prevBalance) => prevBalance - 400);
  };

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    // Проверяем, был ли уже показан preloader в этой сессии
    const preloaderShown = sessionStorage.getItem("preloaderShown");
    if (!preloaderShown) {
      setLoading(true); // Устанавливаем состояние загрузки в true, чтобы показать preloader
      const preloaderTimeout = setTimeout(() => {
        // Устанавливаем значение в sessionStorage после завершения загрузки
        sessionStorage.setItem("preloaderShown", "true");
        setLoading(false);
      }, 3000); // Имитируем задержку в 3 секунды, чтобы показать preloader

      return () => clearTimeout(preloaderTimeout);
    } else {
      setLoading(false);
    }
  }, []);

  // const [loading, setLoading] = useState(false);

  // useEffect(() => {
  //   const preloaderShown = sessionStorage.getItem('preloaderShown');

  //   if (!preloaderShown) {
  //     sessionStorage.setItem('preloaderShown', 'true');
  //     setLoading(true); // Показываем preloader, если это первое посещение в сессии
  //   }

  //   const fetchData = async () => {
  //     try {
  //       // Здесь ваш запрос к API или другому источнику данных
  //       const response = await fetch('https://api.example.com/data');
  //       const data = await response.json();
  //       // После получения данных можно обновить состояние и скрыть preloader
  //       setLoading(false);
  //     } catch (error) {
  //       setLoading(false); // Если произошла ошибка, также скрываем preloader
  //       console.error('Error fetching data:', error);
  //     }
  //   };

  //   fetchData();
  // }, []);

  const league = getLeague(balance);

  const [showPage, setShowPage] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowPage(true);
    }, 50); // Задержка перед появлением страницы
  }, []);

  const [showCup, setShowCup] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowCup(true);
    }, 100); // Задержка перед появлением страницы
  }, []);

  return (
    <>
      {loading ? (
        <Loader />
      ) : (
        // <CSSTransition
        //   in={showPage}
        //   timeout={50}
        //   className="main-animation"
        //   unmountOnExit
        // >
        <div>
          <div className="page">
            <div
              className={`background-position background-color__type-${league}`}
            ></div>
            <div className="container position-relative">
              <div className="page__content">
                <button
                  onClick={() => {
                    navigate("/squad");
                  }}
                  className="black-transparent-btn"
                >
                  <div className="Jh2w">
                    <div className="Li2p">
                      <div className="Po2s">Join squad</div>
                      <div className="Iu3d">
                        <svg
                          width="7"
                          height="12"
                          viewBox="0 0 7 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                          className="icon-shrink icon-shrink__arrow"
                        >
                          <path
                            d="m1 1 5 5-5 5"
                            stroke="currentColor"
                            strokeWidth="2"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          ></path>
                        </svg>
                      </div>
                    </div>
                  </div>
                </button>
                <div className="Jh2w Poj3">
                  <CSSTransition
                    in={showCup}
                    timeout={500}
                    className="scale-enter"
                    unmountOnExit
                  >
                    <div>
                      <div className="Li2p">
                        <div className="balance-icon"></div>
                        <div className="kUe3">{addCommas(balance)}</div>
                      </div>
                    </div>
                  </CSSTransition>
                </div>
                <div className="Jh2w">
                  <div
                    onClick={() =>
                      navigate(`/league?assetLeague=${league}&type=miners`)
                    }
                    className="Li2p Z5dC Hexc"
                  >
                    <CSSTransition
                      in={showCup}
                      timeout={500}
                      className="scale-enter"
                      unmountOnExit
                    >
                      <div>
                        <div
                          style={{
                            display: "flex",
                          }}
                        >
                          <div
                            className={`icon-cup-position icon-cup__${league}`}
                          ></div>
                          <div className="KI3d">{`${
                            league === "bronze"
                              ? `Bronze`
                              : league === "silver"
                              ? `Silver`
                              : league === "gold"
                              ? `Gold`
                              : league === "platinum"
                              ? `Platinum`
                              : `Diamond`
                          }`}</div>
                          <div className="lDtF">
                            <svg
                              width="7"
                              height="12"
                              viewBox="0 0 7 12"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                              className="icon-shrink icon-shrink__arrow"
                            >
                              <path
                                d="m1 1 5 5-5 5"
                                stroke="currentColor"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                              ></path>
                            </svg>
                          </div>
                        </div>
                      </div>
                    </CSSTransition>
                  </div>
                </div>
                {/* <button onClick={handleIncrement} style={{}}>
                  Увеличить
                </button>
                <button onClick={handleDecrement} style={{}}>
                  Уменьшить
                </button> */}
                {/* <Field /> */}
                <div className="Igw3">
                  <div className="Li2p">
                    <div className="icon-lighting"></div>
                    <div className="XVNe iXk6">{`${
                      league === "bronze"
                        ? addCommas(balance)
                        : league === "silver"
                        ? addCommas(balance - 30000)
                        : league === "gold"
                        ? addCommas(balance - 50000)
                        : league === "platinum"
                        ? addCommas(balance - 100000)
                        : addCommas(balance - 300000)
                    }`}</div>
                    <div className="EWRO">
                      /
                      <div className="Rs1m">{`${
                        league === "bronze"
                          ? `30,000`
                          : league === "silver"
                          ? `20,000`
                          : league === "gold"
                          ? `50,000`
                          : league === "platinum"
                          ? `200,000`
                          : `∞`
                      }`}</div>
                    </div>
                  </div>
                </div>
                <div className="E3dv">
                  <div className="Li2p">
                    <TabButton name={"fren"} />
                    <TabButton name={"earn"} />
                  </div>
                </div>
                <div className="YecU">
                  <ProgressBar balance={balance} />
                </div>
              </div>
            </div>
          </div>
        </div>
        // </CSSTransition>
      )}
    </>
  );
}

