import { BrowserRouter as Router, Route, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";
import { CSSTransition } from "react-transition-group";
import "./style.scss";
import { BackButton } from "@twa-dev/sdk/react";
import WebApp from "@twa-dev/sdk";
import getLeague from "../../utils/getLeague";

export default function Referral() {
  const navigate = useNavigate();

  const [balance, setBalance] = useState(() => {
    // При первом запуске компонента проверяем localStorage на наличие баланса
    const storedBalance = localStorage.getItem("balance");
    return storedBalance ? parseFloat(storedBalance) : 0;
  });

  useEffect(() => {
    // При изменении баланса сохраняем его в localStorage
    localStorage.setItem("balance", balance.toString());
  }, [balance]);
  const league = getLeague(balance);

  const [showPage, setShowPage] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setShowPage(true);
    }, 50); // Задержка перед появлением страницы
  }, []);

  return (
    <>
      <BackButton onClick={() => navigate("/fren")} />
      <div className="page">
        <div className="background-yellow theme-yellow"></div>
        <div className="container position-relative scrolling">
          <div className="page__content uY4S">
          <CSSTransition
              in={showPage}
              timeout={50}
              className="referral-animation"
              unmountOnExit
            >
            <div>
              <div className="iosW">Invite fren, get bonus</div>
            <div>
              <div className="B0WY">
                <div className="LIcp">
                  <div className="referral-icon referral-icon__pixelcoin-icon"></div>
                </div>
                <div className="LIcp">
                  <div className="nnot">
                    Invite fren
                    <br />
                    <div className="Li2p">
                      <div className="penny-icon vBwQ bv0r"></div>
                      <div className="R35h JG3Z bv0r">
                        {league === "bronze"
                          ? `1,000`
                          : league === "silver"
                          ? `1,500`
                          : league === "gold"
                          ? `2,000`
                          : league === "platinum"
                          ? `2,500`
                          : `3,000`}
                      </div>{" "}
                      for you and fren
                    </div>
                  </div>
                </div>
              </div>
              <div className="B0WY">
                <div className="IY3i">
                  <div className="referral-icon referral-icon__prem-icon"></div>
                </div>
                <div className="IY3i">
                  <div className="nnot">
                    Fren with{" "}
                    <a
                      onClick={() => {
                        WebApp.openTelegramLink("https://t.me/premium");
                      }}
                      className="prem-link"
                    >
                      Telegram Premium
                    </a>
                    <br />
                    <div className="lDtF nnot">
                      <div className="Li2p">
                        <div className="penny-icon vBwQ bv0r"></div>
                        <div className="R35h JG3Z bv0r">
                          {league === "bronze"
                            ? `1,500`
                            : league === "silver"
                            ? `2,250`
                            : league === "gold"
                            ? `3,000`
                            : league === "platinum"
                            ? `3,750`
                            : `4,500`}
                        </div>{" "}
                        for you and fren
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="Gc12">Fren level up bonus</div>
            <div className="position-relative">
              <div>
                <div className="sJs1 IYwx">Level up</div>
                <div className="IUec JHe8">Regular fren</div>
                <div className="IUec Khw3">Premium</div>
              </div>
              <div>
                <div className="JG3Z">
                  <div className="referral-league-icon referral-league-icon__silver ebYk"></div>
                  <div className="Ibf1">Silver</div>
                </div>
                <div>
                  <div className="LpsI">
                    <div className="YRj2 R35h">+1,500</div>
                  </div>
                  <div className="IYw2">
                    <div className="YRj2 R35h">+2,250</div>
                  </div>
                </div>
              </div>
              <div>
                <div className="JG3Z">
                  <div className="referral-league-icon referral-league-icon__gold IUxe"></div>
                  <div className="Ld9i">Gold</div>
                </div>
                <div>
                  <div className="Jw4e">
                    <div className="YRj2 R35h">+2,000</div>
                  </div>
                  <div className="UKn3">
                    <div className="YRj2 R35h">+3,000</div>
                  </div>
                </div>
              </div>
              <div>
                <div className="JG3Z">
                  <div className="referral-league-icon referral-league-icon__platinum Jwn3"></div>
                  <div className="Khe8">Platinum</div>
                </div>
                <div>
                  <div className="U0ex">
                    <div className="YRj2 R35h">+2,500</div>
                  </div>
                  <div className="Ieo1">
                    <div className="YRj2 R35h">+3,750</div>
                  </div>
                </div>
              </div>
              <div>
                <div className="JG3Z">
                  <div className="referral-league-icon referral-league-icon__diamond JUYe"></div>
                  <div className="Khd1">Diamonds</div>
                </div>
                <div>
                  <div className="GFwe">
                    <div className="YRj2 R35h">+3,000</div>
                  </div>
                  <div className="Usyw">
                    <div className="YRj2 R35h">+4,500</div>
                  </div>
                </div>
              </div>
            </div>
            <div
              onClick={() => {
                WebApp.openTelegramLink(
                  "https://t.me/share/url?text=%F0%9F%8E%81%20%2B2.5k%20Pixelcoin%20as%20a%20first-time%20gift%0A%F0%9F%8E%81%F0%9F%8E%81%F0%9F%8E%81%20%2B50k%20Pixelcoin%20if%20you%20have%20Telegram%20Premium%20%40PremiumBot&url=https://t.me/pixelcoinbot/click?startapp=5668476910"
                );
              }}
              className="fren-footer-btn"
            >
              <div className="Jh2w">
                <div className="Li2p">
                  <div className="Iy4o">Invite a fren</div>
                </div>
              </div>
            </div>
            </div>
            </CSSTransition>
          </div>
        </div>
      </div>
    </>
  );
}
