import "./style.scss";
import getLeague from "../../utils/getLeague";

export default function ProgressBar(props) {
  const league = getLeague(props.balance);
  return (
    <div className="progress-bar">
      <div
        className={`progress-bar-line progress-bar-color_type-${
          league === "bronze"
            ? "bronze"
            : league === "silver"
            ? "silver"
            : league === "gold"
            ? "gold"
            : league === "platinum"
            ? "platinum"
            : "diamond"
        }`}
        style={{
          width: `${
            // (props.balance < 3000 && (props.balance / 3000) * 100) ||
            // (props.balance < 5000 && ((props.balance - 3000) / 2000) * 100) ||
            // (props.balance < 10000 && ((props.balance - 5000) / 5000) * 100) ||
            // (props.balance < 30000 &&
            //   ((props.balance - 10000) / 20000) * 100) ||
            // (props.balance >= 30000 && 100)
            league === "bronze"
            ? (props.balance / 30000) * 100
            : league === "silver"
            ? ((props.balance - 30000) / 20000) * 100
            : league === "gold"
            ? ((props.balance - 50000) / 50000) * 100
            : league === "platinum"
            ? ((props.balance - 100000) / 200000) * 100
            : 100
          }%`,
        }}
      ></div>
    </div>
  );
}

